pper {\r\n\twidth: 660px;\r\n\tmargin: 0 auto;\r\n\tpadding: 50px 0;\r\n  }\r\n  table {\r\n\tdisplay: table;\r\n\twidth: 100%;\r\n\tborder-bottom: 1px solid #ededed;\r\n\tborder-collapse: collapse;\r\n\tborder-spacing: 0;\r\n\tfont-size: 14px;\r\n\tline-height: 2;\r\n\tmargin: 0 0 20px;\r\n  }\r\n\r\n  td, th {\r\n\tdisplay: table-cell;\r\n\ttext-align: left;\r\n\tborder-top: 1px solid #ededed;\r\n\tpadding: 6px 10px 6px 0;\r\n\tfont-weight: normal;\r\n  }\r\n  th {\r\n\tfont-weight: bold;\r\n\ttext-transform: uppercase;\r\n  }\r\n  h3 {\r\n\tfont-size: 22px;\r\n\tmargin: 40px 0 5px;\r\n\tclear: both;\r\n\tdisplay: block;\r\n\tfont-weight: bold;\r\n  }\r\n\r\n}\r\n\r\n/* =Purchase Summary Tables\r\n-------------------------------------------------------------- */\r\ntable#give_donation_receipt_products,\r\ntable#give_donation_receipt {\r\n  width: 100%;\r\n}\r\n\r\ntable#give_donation_receipt {\r\n  td, th, td, th {\r\n\ttext-align: left;\r\n  }\r\n\r\n  li {\r\n\tlist-style: none;\r\n\tmargin: 0 0 8px 10px;\r\n  }\r\n\r\n  ul {\r\n\tmargin: 0;\r\n\tpadding: 0;\r\n  }\r\n\r\n  .give_receipt_payment_status.pending,\r\n  .give_receipt_payment_status.revoked,\r\n  .give_receipt_payment_status.failed {\r\n    color: #f73f2e;\r\n  }\r\n\r\n}\r\n","/**\r\n * Give Frontend Forms SCSS\r\n *\r\n * @package     Give\r\n * @subpackage  SCSS/Frontend\r\n * @copyright   Copyright (c) 2015, WordImpress\r\n * @license     http://opensource.org/licenses/gpl-2.0.php GNU Public License\r\n*/\r\n\r\n/*------------------------------------\r\nLayout\r\n--------------------------------------*/\r\n.give-clearfix {\r\n  @include clearfix();\r\n}\r\n\r\n[id*='give-form'] {\r\n\r\n  fieldset {\r\n\tborder: none;\r\n  }\r\n\r\n  .give-form-title {\r\n\tmargin: 0 0 15px;\r\n  }\r\n\r\n  div#give-sidebar-left {\r\n\twidth: 38%;\r\n\tfloat: left;\r\n  }\r\n\r\n  div.images {\r\n\tmargin: 0 0 15px;\r\n\timg {\r\n\t  display: block;\r\n\t  width: 100%;\r\n\t  height: auto;\r\n\t  box-shadow: none