  //Form Layout\r\n  .form-row {\r\n\t@include clearfix();\r\n\t@include box-sizing(border-box);\r\n\tmargin: 0 0 15px;\r\n\tpadding: 0;\r\n\r\n\tlabel {\r\n\t  display: block;\r\n\t  margin: 0 0 5px;\r\n\t  position: relative;\r\n\t  line-height: 1.7;\r\n\r\n\t  &.checkbox {\r\n\t\tdisplay: inline;\r\n\t  }\r\n\r\n\t}\r\n\r\n\tselect, textarea, input[type=\"text\"], input[type=\"tel\"], input[type=\"email\"], input[type=\"password\"] {\r\n\t  @include box-sizing(border-box);\r\n\t  width: 100%;\r\n\t  border-radius: 0;\r\n\t  outline: none;\r\n\t  color: $dark;\r\n\r\n\t  &.required {\r\n\t\tcolor: $dark;\r\n\t  }\r\n\r\n\t}\r\n\r\n  }\r\n\r\n  .form-row-first,\r\n  .form-row-last,\r\n  .form-row-two-thirds {\r\n\tfloat: left;\r\n\twidth: 48.5%;\r\n\toverflow: visible;\r\n  }\r\n\r\n  .form-row-two-thirds {\r\n\tfloat: left;\r\n\twidth: 65.75%;\r\n\t&.form-row-first {\r\n\t  margin-left: 0;\r\n\t}\r\n  }\r\n\r\n  .form-row-one-third {\r\n\tfloat: left;\r\n\twidth: 31.25%;\r\n\tmargin-left: 3%;\r\n\r\n\t&.form-row-first {\r\n\t  margin-left: 0;\r\n\t}\r\n  }\r\n\r\n  .form-row-last {\r\n\tfloat: right;\r\n  }\r\n\r\n  .form-row-wide {\r\n\t@include clearfix();\r\n\tclear: both;\r\n  }\r\n\r\n  .give-required-indicator {\r\n\tcolor: $red;\r\n  }\r\n\r\n  .card-expiration {\r\n\t> select {\r\n\t  width: 44%;\r\n\t  margin: 0;\r\n\t}\r\n\t> span.exp-divider {\r\n\t  display: inline;\r\n\t  text-align: center;\r\n\t}\r\n\r\n\tselect.card-expiry-year {\r\n\t  float: right;\r\n\t}\r\n\r\n  }\r\n\r\n}\r\n\r\n/*---------------------------------\r\nForm Price & Amount\r\n-----------------------------------*/\r\n\r\nform[id*='give-form'] {\r\n\r\n  .give-donation-amount {\r\n\tmargin: 0 0 15px;\r\n\r\n\t.give-currency-symbol {\r\n\t  @include give-currency();\r\n\r\n\t  &.give-currency-position-before {\r\n\t\tborder-left: 1px solid $borders;\r\n\t\tborder-right: none;\r\n\t  }\r\n\t  &.give-currency-position-after {\r\n\t\tborder-left: none;\r\n\t\tborder-right: 1px solid $borders;\r\n\t  }\r\n\r\n\t}\r\n\r\n\t#give-amount {\r\n\t  @include give-input();\r\n\t  width: 125px;\r\n\r\n\t  &.invalid-amount {\r\n\t\tborder: 1px solid red;\r\n\t  }\r\n\r\n\t}\r\n\r\n\tspan.give-final-total-amount {\r\n\t  @include give-input();\r\n\t  cursor: not-allowed;\r\n\t  background: #FFF;\r\n\t}\r\n\r\n  }\r\n\r\n  #give-final-total-wrap {\r\n\tmargin: 25px 0;\r\n\tpadding: 0;\r\n\t@include clearfix();\r\n\r\n\t.give-donation-total-label {\r\n\t  @include give-currency();\r\n\t  border: 1px solid $borders;\r\n\t}\r\n\r\n\t.give-final-total-amount {\r\n\t  font-size: $font-size-medium;\r\n\t  @include give-input();\r\n\t  border-left: none;\r\n\t}\r\n\r\n  }\r\n\r\n}\r\n\r\n/*---------------------------------\r\nNotices, Warnings & Errors\r\n-----------------------------------*/\r\n\r\n.give_error, .give_success {\r\n  position: relative;\r\n  margin: 15px 0;\r\n  background: #F9F9F9;\r\n  padding: 1em 1em 1em 2em;\r\n  border-left: 4px solid $red;\r\n  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.125);\r\n  clear: both;\r\n\r\n  > p {\r\n\tmargin: 0 !important;\r\n  }\r\n\r\n  &:before {\r\n\t@include iconstyle();\r\n\tposition: absolute;\r\n\ttop: 50%;\r\n\tmargin-top: -15px;\r\n\tleft: -17px;\r\n\tbackground-color: $red;\r\n\tcolor: #FFF;\r\n\twidth: 30px;\r\n\theight: 30px;\r\n\tborder-radius: 100%;\r\n\ttext-align: center;\r\n\tline-height: 30px;\r\n\ttext-shadow: 1px 1px rgba(0, 0, 0, 0.5);\r\n\tcontent: '\\f02d';\r\n  }\r\n\r\n  &.give_warning {\r\n\tborder-color: $orange;\r\n\r\n\t&:before {\r\n\t  background-color: $orange;\r\n\t  text-shadow: 1px 1px rgba(0, 0, 0, 0.1);\r\n\t}\r\n\r\n  }\r\n}\r\n\r\n.give_success {\r\n  border-color: $green;\r\n\r\n  &:before {\r\n\tbackground-color: $green;\r\n\tcontent: '\\e601';\r\n\ttext-shadow: 1px 1px rgba(0, 0, 0, 0.2);\r\n  }\r\n\r\n}\r\n\r\n/*---------------------------------\r\nRadio Lists\r\n-----------------------------------*/\r\n#give-donation-level-radio-list {\r\n  //this is the radio list for the various user set donation levels\r\n  margin: 0 0 15px;\r\n  padding: 0;\r\n  list-style: none;\r\n\r\n  > li {\r\n\r\n\tmargin: 0 0 5px;\r\n\tpadding: 0;\r\n\r\n\tlabel {\r\n\t  cursor: pointer;\r\n\t}\r\n\r\n\tinput[type=\"radio\"] {\r\n\t  margin: 0 5px 0 0;\r\n\t  outline: none;\r\n\t}\r\n  }\r\n\r\n}\r\n\r\n#give-gateway-radio-list {\r\n  //this is the radio list for to select a gateway\r\n  @include clearfix();\r\n  float: left;\r\n  margin: 0 0 25px;\r\n  padding: 0;\r\n  list-style: none;\r\n\r\n  > li {\r\n\tfloat: left;\r\n\tmargin-right: 15px;\r\n\tmargin-left: 0;\r\n\tlist-style-type: none;\r\n\r\n\t.give-gateway-option {\r\n\t  cursor: pointer;\r\n\r\n\t  input[type=\"radio\"] {\r\n\t\tmargin: 0 5px 0 0;\r\n\t\toutline: none;\r\n\t  }\r\n\t}\r\n\r\n  }\r\n\r\n}\r\n\r\n/*---------------------------------\r\nDonation Level: Dropdown/Select\r\n-----------------------------------*/\r\nform[id*='give-form'] {\r\n  select.give-select-level {\r\n\toutline: none;\r\n\tborder-radius: 0;\r\n\tmin-width: 155px;\r\n\tborder: 1px solid $borders;\r\n\tmargin: 0 0 15px;\r\n  }\r\n}\r\n\r\n/*---------------------------------\r\nButtons\r\n-----------------------------------*/\r\n\r\n#give-donation-level-button-wrap {\r\n  @include clearfix();\r\n  margin: 0 0 15px;\r\n  padding: 0;\r\n  clear: both;\r\n  list-style: none;\r\n\r\n  > li {\r\n\tlist-style-type: none;\r\n\tfloat: left;\r\n\tmargin: 0 5px 5px 0;\r\n  }\r\n\r\n}\r\n\r\n.give_terms_links {\r\n  float: right;\r\n}\r\n\r\n/*---------------------------------\r\nCC Validation\r\n-----------------------------------*/\r\n\r\n#give_secure_site_wrapper {\r\n  padding: 4px 4px 15px 0;\r\n  font-size: 16px;\r\n  line-height: 20px;\r\n  //Secure padlock (SSL/HTTPS)\r\n  span.padlock {\r\n\t@include iconstyle();\r\n\t@extend .give-icon-locked;\r\n\tfont-size: 16px;\r\n\tline-height: 20px;\r\n\r\n\t:before {\r\n\t  display: inline-block;\r\n\t}\r\n  }\r\n}\r\n\r\n#give_purchase_form_wrap {\r\n  input.give-input.card-number.valid {\r\n\tbackground-image: url(../assets/images/tick.png);\r\n\tbackground-repeat: no-repeat;\r\n\tbackground-position: 98% 50%;\r\n\tbackground-size: 16px;\r\n\t&.floatlabel-input {\r\n\t  background-image: url(../assets/images/tick-alt.png);\r\n\t  background-position: 100% 50%;\r\n\t  background-size: 72px 16px;\r\n\t}\r\n  }\r\n\r\n  span.card-type {\r\n\tposition: absolute;\r\n\ttop: -2px;\r\n\tright: 0;\r\n\twidth: 37px;\r\n\theight: 26px;\r\n\tbackground-size: 37px 26px !important;\r\n  }\r\n\r\n  .floatlabel {\r\n\tspan.card-type {\r\n\t  top: 11px;\r\n\t  right: 11px;\r\n\t}\r\n  }\r\n\r\n  span.card-type.off {\r\n\tdisplay: none;\r\n  }\r\n  span.card-type.visa {\r\n\tbackground: url(../assets/images/visa.png) no-repeat;\r\n  }\r\n  span.card-type.mastercard {\r\n\tbackground: url(../assets/images/mastercard.png) no-repeat;\r\n  }\r\n  span.card-type.discover {\r\n\tbackground: url(../assets/images/discover.png) no-repeat;\r\n  }\r\n  span.card-type.amex {\r\n\tbackground: url(../assets/images/amex.png) no-repeat;\r\n  }\r\n  span.card-type.maestro {\r\n\tbackground: url(../assets/images/maestro.png) no-repeat;\r\n  }\r\n  span.card-type.dinersclub {\r\n\tbackground: url(../assets/images/diners-club.png) no-repeat;\r\n  }\r\n  span.card-type.jcb {\r\n\tbackground: url(../assets/images/jcb.png) no-repeat;\r\n  }\r\n  span.card-type.dankort {\r\n\tbackground: url(../assets/images/dankort.png) no-repeat;\r\n  }\r\n  span.card-type.unionpay {\r\n\tbackground: url(../assets/images/unionpay.png) no-repeat;\r\n  }\r\n}\r\n\r\n/*---------------------------------\r\nLoading Animation\r\n-----------------------------------*/\r\n\r\n/* Loading Elipsis */\r\n@-webkit-keyframes opacity {\r\n  0% {\r\n\topacity: 1;\r\n  }\r\n  100% {\r\n\topacity: 0;\r\n  }\r\n}\r\n\r\n@-moz-keyframes opacity {\r\n  0% {\r\n\topacity: 1;\r\n  }\r\n  100% {\r\n\topacity: 0;\r\n  }\r\n}\r\n\r\n.give-loading-text {\r\n\r\n  span.elipsis {\r\n\t-webkit-animation-name: opacity;\r\n\t-webkit-animation-duration: 1s;\r\n\t-webkit-animation-iteration-count: infinite;\r\n\r\n\t-moz-animation-name: opacity;\r\n\t-moz-animation-duration: 1s;\r\n\t-moz-animation-iteration-count: infinite;\r\n\r\n\t&:nth-child(1) {\r\n\t  -webkit-animation-delay: 100ms;\r\n\t  -moz-animation-delay: 100ms;\r\n\t}\r\n\t&:nth-child(2) {\r\n\t  -webkit-animation-delay: 300ms;\r\n\t  -moz-animation-delay: 300ms;\r\n\t}\r\n\t&:nth-child(3) {\r\n\t  -webkit-animation-delay: 500ms;\r\n\t  -moz-animation-delay: 500ms;\r\n\t}\r\n  }\r\n}\r\n\r\nform[id*='give-form'] {\r\n\r\n  p.give-loading-text {\r\n\tdisplay: none;\r\n\tfloat: right;\r\n\tmargin: 0;\r\n\tpadding: 0;\r\n\tfont-style: italic;\r\n\tline-height: 20px;\r\n\tfont-size: 16px;\r\n\r\n\t&.give-updating-price-loader {\r\n\t  float: left;\r\n\t  margin: 6px 0 0 20px;\r\n\t}\r\n\r\n  }\r\n  .give-loading-animation {\r\n\t@include iconstyle();\r\n\tfloat: left;\r\n\twidth: 20px;\r\n\theight: 20px;\r\n\tline-height: 20px;\r\n\tfont-size: 16px;\r\n\tmargin: 0 3px 0 0;\r\n\tbackground-size: 20px 20px;\r\n\tbackground-repeat: no-repeat;\r\n\tbackground-color: transparent;\r\n\t@extend .icon-spinner2;\r\n\r\n\t&:before {\r\n\t  @extend .fa-spin;\r\n\t}\r\n  }\r\n\r\n}\r\n\r\n/* Login and Register */\r\n\r\n#give-login-account-wrap {\r\n  @include clearfix();\r\n\r\n  > p {\r\n\tfloat: left;\r\n\tmargin: 0 0 15px;\r\n\tpadding: 0;\r\n  }\r\n\r\n  p.give-login-message {\r\n\tmargin-right: 10px;\r\n  }\r\n\r\n}\r\n\r\n/* User Login */\r\n#give-user-login-submit, .give-submit-button-wrap {\r\n\r\n  input.give-submit, span.give-loading-animation {\r\n\tfloat: left;\r\n  }\r\n\r\n  span.give-loading-animation {\r\n\tmargin: 3px 6px;\r\n\tdisplay: none;\r\n  }\r\n\r\n}\r\n\r\np.give-recurring-login-toggle {\r\n  font-style: italic;\r\n}\r\n\r\n/*---------------------------------\r\nModal - Magnific Popup\r\n-----------------------------------*/\r\n\r\n//Show/Hide elements in Magnific Popup\r\ndiv.give-form-wrap.mfp-hide {\r\n  display: block !important;\r\n}\r\n\r\ndiv.mfp-content {\r\n  .give-form-content-wrap, .give-donation-amount, .give-select-level {\r\n\tdisplay: none;\r\n  }\r\n\r\n  //Basic Styles\r\n  fieldset {\r\n\tp, ol, ul {\r\n\t  margin-bottom: 1.2em;\r\n\t  line-height: 1.4;\r\n\t}\r\n  }\r\n\r\n}\r\n\r\n/*---------------------------------\r\nResponsiveness\r\n-----------------------------------*/\r\n@media (max-width: 768px) {\r\n\r\n  /* Layout */\r\n  [id*='give-form'] {\r\n\r\n\tdiv.images, div.summary {\r\n\t  width: 100%;\r\n\t  float: none;\r\n\t}\r\n\r\n  }\r\n\r\n  /* Form */\r\n  form[id*='give-form'] {\r\n\r\n\t#give-card-name-wrap, .card-expiration {\r\n\t  width: 46%;\r\n\t}\r\n\r\n  }\r\n\r\n}\r\n","/**\r\n * Give CSS Variables\r\n */\r\n\r\n$green: #7ad03a;\r\n$red: #a00;\r\n$orange: #ffba00;\r\n$blue: #2ea2cc;\r\n$dark: #333;\r\n$borders: #ccc;\r\n$light-gray: #EDEDED;\r\n\r\n//Status\r\n$status-default: #777;\r\n$status-complete: $green;\r\n$status-refunded: $status-default;\r\n$status-subscription: #5bc0de;\r\n$status-revoked: #d9534f;\r\n\r\n//Font sizes\r\n$font-size-medium: 18px;\r\n\r\n$primary: #007ba4;\r\n// Primary colour for buttons (alt)\r\n$primarytext: desaturate(lighten($primary, 50%), 18%);\r\n// Text on primary colour bg\r\n\r\n$secondary: desaturate(lighten($primary, 40%), 21%);\r\n// Secondary buttons\r\n$secondarytext: desaturate(darken($secondary, 60%), 21%);\r\n// Text on secondary colour bg\r\n\r\n$highlight: adjust-hue($primary, 150deg);\r\n// Prices, In stock labels, sales flash\r\n$highlightext: desaturate(lighten($highlight, 50%), 18%);\r\n// Text on highlight colour bg\r\n\r\n$contentbg: #fff;\r\n// Content BG - Tabs (active state)\r\n$subtext: #777;\r\n// small, breadcrumbs etc","/* Magnific Popup CSS */\n\n\n////////////////////////\n//\n// Contents:\n//\n// 1. Default Settings\n// 2. General styles\n//    - Transluscent overlay\n//    - Containers, wrappers\n//    - Cursors\n//    - Helper classes\n// 3. Appearance\n//    - Preloader & text that displays error messages\n//    - CSS reset for buttons\n//    - Close icon\n//    - \"1 of X\" counter\n//    - Navigation (left/right) arrows\n//    - Iframe content type styles\n//    - Image content type styles\n//    - Media query where size of arrows is reduced\n//    - IE7 support\n//\n////////////////////////\n\n////////////////////////\n// 1. Default Settings\n////////////////////////\n\n$mfp-overlay-color: #0b0b0b !default;\n$mfp-overlay-opacity: 0.8 !default;\n$mfp-shadow: 0 0 8px rgba(0, 0, 0, 0.6) !default;\n// shadow on image or iframe\n$mfp-popup-padding-left: 8px !default;\n// Padding from left and from right side\n$mfp-popup-padding-left-mobile: 6px !default;\n// Same as above, but is applied when width of window is less than 800px\n\n$mfp-z-index-base: 1040 !default;\n// Base z-index of popup\n$mfp-include-arrows: true !default;\n// include styles for nav arrows\n$mfp-controls-opacity: 0.65 !default;\n$mfp-controls-color: #FFF !default;\n$mfp-controls-border-color: #3F3F3F !default;\n$mfp-inner-close-icon-color: #333 !default;\n$mfp-controls-text-color: #CCC !default;\n// Color of preloader and \"1 of X\" indicator\n$mfp-controls-text-color-hover: #FFF !default;\n$mfp-IE7support: true !default;\n// Very basic IE7 support\n\n// Iframe-type options\n$mfp-include-iframe-type: false !default;\n$mfp-iframe-padding-top: 40px !default;\n$mfp-iframe-background: #000 !default;\n$mfp-iframe-max-width: 900px !default;\n$mfp-iframe-ratio: 9/16 !default;\n\n// Image-type options\n$mfp-include-image-type: false !default;\n$mfp-image-background: #444 !default;\n$mfp-image-padding-top: 40px !default;\n$mfp-image-padding-bottom: 40px !default;\n$mfp-include-mobile-layout-for-image: true !default;\n// Removes paddings from top and bottom\n\n// Image caption options\n$mfp-caption-title-color: #F3F3F3 !default;\n$mfp-caption-subtitle-color: #BDBDBD !default;\n\n// A11y\n$mfp-use-visuallyhidden: false !default;\n// Hide content from browsers, but make it available for screen readers\n\n////////////////////////\n// 2. General styles\n////////////////////////\n\n// Transluscent overlay\n.give-modal.mfp-bg {\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  z-index: $mfp-z-index-base + 2;\n  overflow: hidden;\n  position: fixed;\n\n  background: $mfp-overlay-color;\n  opacity: $mfp-overlay-opacity;\n  @if $mfp-IE7support {\n\tfilter: unquote(\"alpha(opacity=#{$mfp-overlay-opacity*100})\");\n  }\n}\n\n// Helper class that hides stuff\n@if $mfp-use-visuallyhidden {\n  // From HTML5 Boilerplate https://github.com/h5bp/html5-boilerplate/blob/v4.2.0/doc/css.md#visuallyhidden\n  .mfp-hide {\n\tborder: 0 !important;\n\tclip: rect(0 0 0 0) !important;\n\theight: 1px !important;\n\tmargin: -1px !important;\n\toverflow: hidden !important;\n\tpadding: 0 !important;\n\tposition: absolute !important;\n\twidth: 1px !important;\n  }\n} @else {\n  .mfp-hide {\n\tdisplay: none !important;\n  }\n}\n\n////////////////////////\n// 3. Appearance\n////////////////////////\n\n// Preloader and text that displays error messages\n.mfp-preloader {\n  color: $mfp-controls-text-color;\n  position: absolute;\n  top: 50%;\n  width: auto;\n  text-align: center;\n  margin-top: -0.8em;\n  left: 8px;\n  right: 8px;\n  z-index: $mfp-z-index-base + 4;\n  a {\n\tcolor: $mfp-controls-text-color;\n\t&:hover {\n\t  color: $mfp-controls-text-color-hover;\n\t}\n  }\n}\n\n// Hide preloader when content successfully loaded\n.mfp-s-ready {\n  .mfp-preloader {\n\tdisplay: none;\n  }\n}\n\n// Hide content when it was not loaded\n.mfp-s-error {\n  .mfp-content {\n\tdisplay: none;\n  }\n}\n\n// CSS-reset for buttons\nbutton {\n  &.mfp-close,\n  &.mfp-arrow {\n\toverflow: visible;\n\tcursor: pointer;\n\tbackground: transparent;\n\tborder: 0;\n\t-webkit-appearance: none;\n\tdisplay: block;\n\toutline: none;\n\tpadding: 0;\n\tz-index: $mfp-z-index-base + 6;\n\t-webkit-box-shadow: none;\n\tbox-shadow: none;\n  }\n  &::-moz-focus-inner {\n\tpadding: 0;\n\tborder: 0\n  }\n}\n\n// Close icon\n.mfp-close {\n  width: 44px;\n  height: 44px;\n  line-height: 44px;\n\n  position: absolute;\n  right: 0;\n  top: 0;\n  text-decoration: none;\n  text-align: center;\n  opacity: $mfp-controls-opacity;\n  @if $mfp-IE7support {\n\tfilter: unquote(\"alpha(opacity=#{$mfp-controls-opacity*100})\");\n  }\n  padding: 0 0 18px 10px;\n  color: $mfp-controls-color;\n\n  font-style: normal;\n  font-size: 28px;\n  font-family: Arial, Baskerville, monospace;\n\n  &:hover,\n  &:focus {\n\topacity: 1;\n\t@if $mfp-IE7support {\n\t  filter: unquote(\"alpha(opacity=#{1*100})\");\n\t}\n  }\n\n  &:active {\n\ttop: 1px;\n  }\n}\n\n.mfp-close-btn-in {\n  .mfp-close {\n\tcolor: $mfp-inner-close-icon-color;\n\tbackground-color: transparent;\n  }\n}\n\n.mfp-image-holder,\n.mfp-iframe-holder {\n  .mfp-close {\n\tcolor: $mfp-controls-color;\n\tright: -6px;\n\ttext-align: right;\n\tpadding-right: 6px;\n\twidth: 100%;\n  }\n}\n\n// \"1 of X\" counter\n.mfp-counter {\n  position: absolute;\n  top: 0;\n  right: 0;\n  color: $mfp-controls-text-color;\n  font-size: 12px;\n  line-height: 18px;\n  white-space: nowrap;\n}\n\n// Navigation arrows\n@if $mfp-include-arrows {\n  .mfp-arrow {\n\tposition: absolute;\n\topacity: $mfp-controls-opacity;\n\t@if $mfp-IE7support {\n\t  filter: unquote(\"alpha(opacity=#{$mfp-controls-opacity*100})\");\n\t}\n\tmargin: 0;\n\ttop: 50%;\n\tmargin-top: -55px;\n\tpadding: 0;\n\twidth: 90px;\n\theight: 110px;\n\t-webkit-tap-highlight-color: rgba(0, 0, 0, 0);\n\t&:active {\n\t  margin-top: -54px;\n\t}\n\t&:hover,\n\t&:focus {\n\t  opacity: 1;\n\t  @if $mfp-IE7support {\n\t\tfilter: unquote(\"alpha(opacity=#{1*100})\");\n\t  }\n\t}\n\t&:before,\n\t&:after,\n\t.mfp-b,\n\t.mfp-a {\n\t  content: '';\n\t  display: block;\n\t  width: 0;\n\t  height: 0;\n\t  position: absolute;\n\t  left: 0;\n\t  top: 0;\n\t  margin-top: 35px;\n\t  margin-left: 35px;\n\t  border: medium inset transparent;\n\t}\n\n\t&:after,\n\t.mfp-a {\n\n\t  border-top-width: 13px;\n\t  border-bottom-width: 13px;\n\t  top: 8px;\n\t}\n\n\t&:before,\n\t.mfp-b {\n\t  border-top-width: 21px;\n\t  border-bottom-width: 21px;\n\t  opacity: 0.7;\n\t}\n\n  }\n\n  .mfp-arrow-left {\n\tleft: 0;\n\n\t&:after,\n\t.mfp-a {\n\t  border-right: 17px solid $mfp-controls-color;\n\t  margin-left: 31px;\n\t}\n\t&:before,\n\t.mfp-b {\n\t  margin-left: 25px;\n\t  border-right: 27px solid $mfp-controls-border-color;\n\t}\n  }\n\n  .mfp-arrow-right {\n\tright: 0;\n\t&:after,\n\t.mfp-a {\n\t  border-left: 17px solid $mfp-controls-color;\n\t  margin-left: 39px\n\t}\n\t&:before,\n\t.mfp-b {\n\t  border-left: 27px solid $mfp-controls-border-color;\n\t}\n  }\n}\n\n// Iframe content type\n@if $mfp-include-iframe-type {\n  .mfp-iframe-holder {\n\tpadding-top: $mfp-iframe-padding-top;\n\tpadding-bottom: $mfp-iframe-padding-top;\n\t.mfp-content {\n\t  line-height: 0;\n\t  width: 100%;\n\t  max-width: $mfp-iframe-max-width;\n\t}\n\t.mfp-close {\n\t  top: -40px;\n\t}\n  }\n  .mfp-iframe-scaler {\n\twidth: 100%;\n\theight: 0;\n\toverflow: hidden;\n\tpadding-top: $mfp-iframe-ratio * 100%;\n\tiframe {\n\t  position: absolute;\n\t  display: block;\n\t  top: 0;\n\t  left: 0;\n\t  width: 100%;\n\t  height: 100%;\n\t  box-shadow: $mfp-shadow;\n\t  background: $mfp-iframe-background;\n\t}\n  }\n}\n\n// Image content type\n@if $mfp-include-image-type {\n\n  /* Main image in popup */\n  img {\n\t&.mfp-img {\n\t  width: auto;\n\t  max-width: 100%;\n\t  height: auto;\n\t  display: block;\n\t  line-height: 0;\n\t  -webkit-box-sizing: border-box;\n\t  -moz-box-sizing: border-box;\n\t  box-sizing: border-box;\n\t  padding: $mfp-image-padding-top 0 $mfp-image-padding-bottom;\n\t  margin: 0 auto;\n\t}\n  }\n\n  /* The shadow behind the image */\n  .mfp-figure {\n\tline-height: 0;\n\t&:after {\n\t  content: '';\n\t  position: absolute;\n\t  left: 0;\n\t  top: $mfp-image-padding-top;\n\t  bottom: $mfp-image-padding-bottom;\n\t  display: block;\n\t  right: 0;\n\t  width: auto;\n\t  height: auto;\n\t  z-index: -1;\n\t  box-shadow: $mfp-shadow;\n\t  background: $mfp-image-background;\n\t}\n\tsmall {\n\t  color: $mfp-caption-subtitle-color;\n\t  display: block;\n\t  font-size: 12px;\n\t  line-height: 14px;\n\t}\n\tfigure {\n\t  margin: 0;\n\t}\n  }\n  .mfp-bottom-bar {\n\tmargin-top: -$mfp-image-padding-bottom + 4;\n\tposition: absolute;\n\ttop: 100%;\n\tleft: 0;\n\twidth: 100%;\n\tcursor: auto;\n  }\n  .mfp-title {\n\ttext-align: left;\n\tline-height: 18px;\n\tcolor: $mfp-caption-title-color;\n\tword-wrap: break-word;\n\tpadding-right: 36px; // leave some space for counter at right side\n  }\n\n  .mfp-image-holder {\n\t.mfp-content {\n\t  max-width: 100%;\n\t}\n  }\n\n  .mfp-gallery {\n\t.mfp-image-holder {\n\t  .mfp-figure {\n\t\tcursor: pointer;\n\t  }\n\t}\n  }\n\n  @if $mfp-include-mobile-layout-for-image {\n\t@media screen and (max-width: 800px) and (orientation: landscape), screen and (max-height: 300px) {\n\t  /**\n\t   * Remove all paddings around the image on small screen\n\t   */\n\t  .mfp-img-mobile {\n\t\t.mfp-image-holder {\n\t\t  padding-left: 0;\n\t\t  padding-right: 0;\n\t\t}\n\t\timg {\n\t\t  &.mfp-img {\n\t\t\tpadding: 0;\n\t\t  }\n\t\t}\n\t\t.mfp-figure {\n\t\t  // The shadow behind the image\n\t\t  &:after {\n\t\t\ttop: 0;\n\t\t\tbottom: 0;\n\t\t  }\n\t\t  small {\n\t\t\tdisplay: inline;\n\t\t\tmargin-left: 5px;\n\t\t  }\n\t\t}\n\t\t.mfp-bottom-bar {\n\t\t  background: rgba(0, 0, 0, 0.6);\n\t\t  bottom: 0;\n\t\t  margin: 0;\n\t\t  top: auto;\n\t\t  padding: 3px 5px;\n\t\t  position: fixed;\n\t\t  -webkit-box-sizing: border-box;\n\t\t  -moz-box-sizing: border-box;\n\t\t  box-sizing: border-box;\n\t\t  &:empty {\n\t\t\tpadding: 0;\n\t\t  }\n\t\t}\n\t\t.mfp-counter {\n\t\t  right: 5px;\n\t\t  top: 3px;\n\t\t}\n\t\t.mfp-close {\n\t\t  top: 0;\n\t\t  right: 0;\n\t\t  width: 35px;\n\t\t  height: 35px;\n\t\t  line-height: 35px;\n\t\t  background: rgba(0, 0, 0, 0.6);\n\t\t  position: fixed;\n\t\t  text-align: center;\n\t\t  padding: 0;\n\t\t}\n\t  }\n\t}\n  }\n}\n\n// Scale navigation arrows and reduce padding from sides\n@media all and (max-width: 900px) {\n  .mfp-arrow {\n\t-webkit-transform: scale(0.75);\n\ttransform: scale(0.75);\n  }\n  .mfp-arrow-left {\n\t-webkit-transform-origin: 0;\n\ttransform-origin: 0;\n  }\n  .mfp-arrow-right {\n\t-webkit-transform-origin: 100%;\n\ttransform-origin: 100%;\n  }\n  .mfp-container {\n\tpadding-left: $mfp-popup-padding-left-mobile;\n\tpadding-right: $mfp-popup-padding-left-mobile;\n  }\n}\n\n// IE7 support\n// Styles that make popup look nicier in old IE\n@if $mfp-IE7support {\n  .mfp-ie7 {\n\t.mfp-img {\n\t  padding: 0;\n\t}\n\t.mfp-bottom-bar {\n\t  width: 600px;\n\t  left: 50%;\n\t  margin-left: -300px;\n\t  margin-top: 5px;\n\t  padding-bottom: 5px;\n\t}\n\t.mfp-container {\n\t  padding: 0;\n\t}\n\t.mfp-content {\n\t  padding-top: 44px;\n\t}\n\t.mfp-close {\n\t  top: 0;\n\t  right: 0;\n\t  padding-top: 0;\n\t}\n  }\n}\n\n/**\n * Responsive Give Form content within modal\n */\n.give-responsive-mfp-content {\n  form[id*=\"give-form\"] .form-row-one-third, #give-recurring-form .form-row-one-third, form.give-form .form-row-one-third {\n\n\twidth: 100%;\n\tmargin-left: 0;\n\tmargin-right: 0;\n  }\n\n  #give-user-pass-wrap, #give-user-pass-confirm-wrap {\n\tfloat: left;\n\twidth: 48.5%;\n\toverflow: visible;\n  }\n\n  #give-user-pass-confirm-wrap {\n\tfloat: right;\n  }\n\n}","////////////////////////\n//      Settings      //\n////////////////////////\n\n// overlay\n$mfp-overlay-color:                   #0b0b0b;                    // Color of overlay screen\n$mfp-overlay-opacity:                 0.8;                        // Opacity of overlay screen\n$mfp-shadow:                          0 0 8px rgba(0, 0, 0, 0.6); // Shadow on image or iframe\n\n// spacing\n$mfp-popup-padding-left:              8px;                        // Padding from left and from right side\n$mfp-popup-padding-left-mobile:       6px;                        // Same as above, but is applied when width 