   #F3F3F3;                    // Caption title color\n$mfp-caption-subtitle-color:          #BDBDBD;                    // Caption subtitle color\n\n// A11y\n$mfp-use-visuallyhidden:              false; ","/*!\n * Give Layouts SCSS/CSS\n *\n * @description: This file provides layout styles for various themes such as the default WordPress themes and others if applicable; Most themes handle Give's template system just fine ;)\n * @package:     Give\n * @subpackage:  SCSS/Frontend\n * @copyright:   Copyright (c) 2015, WordImpress\n * @license:     http://opensource.org/licenses/gpl-2.0.php GNU Public License\n*/\n\n/**\n * Twenty Thirteen specific styles\n */\n\n.twentythirteen {\n\n  .give_forms {\n\tpadding: 40px 0;\n  }\n\n}\n\n/**\n * Twenty Fourteen specific styles\n */\n.twentyfourteen {\n  .tfgive {\n\tpadding: 12px 10px 0;\n\tmax-width: 474px;\n\tmargin: 0 auto;\n\n\t.give_forms .entry-summary {\n\t  padding: 0 !important;\n\t  margin: 0 0 1.618em !important;\n\t}\n\n\tdiv.give_forms.hentry.has-post-thumbnail {\n\t  margin-top: 0;\n\t}\n\n\t.give_forms {\n\t  .images {\n\t\timg {\n\t\t  margin-bottom: 1em;\n\t\t}\n\t  }\n\t}\n  }\n}\n\n@media screen and (min-width: 673px) {\n  .twentyfourteen {\n\t.tfgive {\n\t  padding-right: 30px;\n\t  padding-left: 30px;\n\t}\n  }\n}\n\n@media screen and (min-width: 1040px) {\n  .twentyfourteen {\n\t.tfgive {\n\t  padding-right: 15px;\n\t  padding-left: 15px;\n\t}\n  }\n}\n\n@media screen and (min-width: 1110px) {\n  .twentyfourteen {\n\t.tfgive {\n\t  padding-right: 30px;\n\t  padding-left: 30px;\n\t}\n  }\n}\n\n@media screen and (min-width: 1218px) {\n  .twentyfourteen {\n\t.tfgive {\n\t  margin-right: 54px;\n\t}\n  }\n  .full-width {\n\t.twentyfourteen {\n\t  .tfgive {\n\t\tmargin-right: auto;\n\t  }\n\t}\n  }\n}\n\n/**\n * Twenty Fifteen specific styles\n */\n.twentyfifteen {\n  .t15give {\n\tpadding-left: 7.6923%;\n\tpadding-right: 7.6923%;\n\tpadding-top: 7.6923%;\n\tmargin-bottom: 7.6923%;\n\tbackground: #fff;\n\tbox-shadow: 0 0 1px rgba(0, 0, 0, 0.15);\n\n\t.page-title {\n\t  margin-left: 0;\n\t}\n\tdiv.summary {\n\t  padding: 0;\n\t}\n  }\n}\n\n@media screen and (min-width: 38.75em) {\n  .twentyfifteen {\n\t.t15give {\n\t  margin-right: 7.6923%;\n\t  margin-left: 7.6923%;\n\t  margin-top: 8.3333%;\n\t}\n  }\n}\n\n@media screen and (min-width: 59.6875em) {\n  .twentyfifteen {\n\t.t15give {\n\t  margin-left: 8.3333%;\n\t  margin-right: 8.3333%;\n\t  padding: 10%;\n\t}\n  }\n\n  .single-product {\n\t.twentyfifteen {\n\t  .entry-summary {\n\t\tpadding: 0 !important;\n\t  }\n\t}\n  }\n}\n\n/*----------------------------------------------\nAvada\n------------------------------------------------*/\n\n.avada-row > div.give-wrap #content {\n  width: 100%;\n}","/**\n * Give Frontend Progress Bar SCSS\n *\n * @package     Give\n * @subpackage  SCSS/Frontend\n * @copyright   Copyright (c) 2015, WordImpress\n * @license     http://opensource.org/licenses/gpl-2.0.php GNU Public License\n*/\n\n.goal-progress{\n\tmargin-bottom: 20px;\n\n\t.income {\n\t\tfont-size: 46px;\n\t\tline-height: 48px;\n\t\tletter-spacing: -1px;\n\t\tcolor: #333;\n\t}\n\n\t.raised{\n\t\tmargin-bottom: 15px;\n\t}\n}\n\n.progress-bar { \n\theight: 20px;  /* Can be anything */\n\tposition: relative;\n\tbackground: #EEE;\n\tborder-radius: 25px;\n\n\t> span {\n\t\tdisplay: block;\n\t\theight: 100%;\n\t\tborder-top-right-radius: 8px;\n\t\tborder-bottom-right-radius: 8px;\n\t\tborder-top-left-radius: 20px;\n\t\tborder-bottom-left-radius: 20px;\n\t\tbackground-color: rgb(43,194,83);\n\t\tposition: relative;\n\t\toverflow: hidden;\n\t}\n}","/* ========================================================================== *\\\n   Components -> Float Label ($components-float-label)\n\\* ========================================================================== */\n\n//\n// Variables\n//\n\n$fl-white             : #fff !default;\n$fl-black             : #333 !default;\n$fl-placeholder       : #aaa !default;\n$fl-borders           : #ccc !default;\n$fl-primary           : #007ba4 !default;\n\n$fl-font-size         : 16px !default;\n$fl-font-size-small   : 11px !default;\n\n$fl-spacing-base      : 24px !default;\n$fl-spacing-half      : $fl-spacing-base / 2 !default;\n$fl-spacing-third     : $fl-spacing-base / 3 !default;\n$fl-spacing-fourth    : $fl-spacing-base / 4 !default;\n\n$fl-transition-speed  : 0.2s !default;\n$fl-transition-easing : ease !default;\n\n//\n// Styles\n//\n\nform.floated-labels {\n\n\t.floatlabel {\n\n\t\tposition: relative;\n\n\t\tinput,\n\t\tselect {\n\t\t\tfont-size: $fl-font-size;\n\t\t\tline-height: normal;\n\t\t\tpadding: $fl-spacing-half;\n\t\t\tborder: 1px solid $fl-borders;\n\t\t}\n\n\t\tselect {\n\t\t\tposition: relative;\n\t\t\t// background-color: $fl-white;\n\t\t\tcolor: $fl-placeholder;\n\t\t\tcursor: pointer;\n\t\t\t-webkit-appearance: none;\n\t\t\t-moz-appearance: none;\n\t\t}\n\n\t\t&.is-active {\n\t\t\tlabel.floatlabel-label {\n\t\t\t\topacity: 1;\n\t\t\t\tvisibility: visible;\n\t\t\t}\n\n\t\t\tinput,\n\t\t\tselect,\n\t\t\ttextarea {\n\t\t\t\tcolor: $fl-black;\n\t\t\t\tpadding-top: $fl-spacing-base - ($fl-spacing-third / 2);\n\t\t\t\tpadding-bottom: $fl-spacing-third / 2;\n\t\t\t}\n\t\t}\n\n\t\t&.is-focused {\n\t\t\tlabel.floatlabel-label {\n\t\t\t\tcolor: $fl-primary;\n\t\t\t}\n\t\t}\n\n\t\tlabel.floatlabel-label {\n\t\t\tposition: absolute;\n\t\t\tdisplay: block;\n\t\t\ttop: $fl-spacing-third;\n\t\t\tleft: $fl-spacing-half;\n\t\t\tcolor: $fl-placeholder;\n\t\t\tfont-size: $fl-font-size-small;\n\t\t\tline-height: 1;\n\t\t\topacity: 0;\n\t\t\tvisibility: hidden;\n\t\t\ttransition: all $fl-transition-speed $fl-transition-easing;\n\t\t\tpadding: 0 1px;\n\n\t\t\t&.is-visible {\n\t\t\t\topacity: 1;\n\t\t\t\tvisibility: visible;\n\t\t\t}\n\t\t}\n\t}\n\n\t.styled {\n\n\t\t&.select {\n\n\t\t\tposition: relative;\n\n\t\t\t&:before,\n\t\t\t&:after {\n\t\t\t\tcontent: '';\n\t\t\t\tposition: absolute;\n\t\t\t\tdisplay: block;\n\t\t\t\ttop: $fl-spacing-base;\n\t\t\t\theight: 2px;\n\t\t\t\twidth: $fl-spacing-fourth;\n\t\t\t\tbackground: $fl-placeholder;\n\t\t\t\tz-index: 2;\n\t\t\t}\n\n\t\t\t&:before {\n\t\t\t\tright: $fl-spacing-half + $fl-spacing-fourth;\n\t\t\t\ttransform: skew(0deg, 45deg);\n\t\t\t}\n\n\t\t\t&:after {\n\t\t\t\tright: $fl-spacing-half;\n\t\t\t\ttransform: skew(0deg, -45deg);\n\t\t\t}\n\t\t}\n\t}\n}\n","/**\n * Give Magnific Modal Custom SCSS\n *\n * @package     Give\n * @subpackage  SCSS/Frontend\n * @copyright   Copyright (c) 2015, WordImpress\n * @license     http://opensource.org/licenses/gpl-2.0.php GNU Public License\n*/\n\n// Wrapper for popup\n.give-modal {\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  z-index: $mfp-z-index-base + 3;\n  position: fixed;\n  outline: none !important;\n  -webkit-backface-visibility: hidden; // fixes webkit bug that can cause \"false\" scrollbar\n\n  form[id*='give-form'] {\n\tmargin-bottom: 0;\n\t.give-submit {\n\t  margin-bottom: 0;\n\t}\n\t.give-payment-mode-label {\n\t  margin-top: 0;\n\t  padding-top: 0;\n\t}\n  }\n\n  // Root container\n  .mfp-container {\n\ttext-align: center;\n\tposition: absolute;\n\twidth: 100%;\n\theight: 100%;\n\tleft: 0;\n\ttop: 0;\n\tpadding: 0 $mfp-popup-padding-left;\n\t-webkit-box-sizing: border-box;\n\t-moz-box-sizing: border-box;\n\tbox-sizing: border-box;\n  }\n\n  // Vertical centerer helper\n  .mfp-container {\n\t&:before {\n\t  content: '';\n\t  display: inline-block;\n\t  height: 100%;\n\t  vertical-align: middle;\n\t}\n  }\n\n  // Remove vertical centering when popup has class `mfp-align-top`\n  .mfp-align-top {\n\t.mfp-container {\n\t  &:before {\n\t\tdisplay: none;\n\t  }\n\t}\n  }\n\n  // Popup content holder\n  .mfp-content {\n\tdisplay: inline-block;\n\tvertical-align: middle;\n\ttext-align: left;\n\tposition: relative;\n\tbackground: #FFF;\n\tpadding: 20px;\n\twidth: auto;\n\tmax-width: 500px;\n\tmargin: 40px auto;\n\tz-index: $mfp-z-index-base + 5;\n\n\t.give-donation-levels-wrap, .give-btn-modal {\n\t  display: none;\n\t}\n\n\t[id*='give-form'] #give-payment-mode-select, [id*='give-form'] #give_purchase_form_wrap {\n\t  display: block;\n\t}\n\n  }\n\n  .mfp-inline-holder,\n  .mfp-ajax-holder {\n\t.mfp-content {\n\t  width: 100%;\n\t  cursor: auto;\n\t}\n  }\n\n  // Cursors\n  .mfp-ajax-cur {\n\tcursor: progress;\n  }\n\n  .mfp-zoom-out-cur {\n\t&, .mfp-image-holder .mfp-close {\n\t  cursor: -moz-zoom-out;\n\t  cursor: -webkit-zoom-out;\n\t  cursor: zoom-out;\n\t}\n  }\n\n  .mfp-zoom {\n\tcursor: pointer;\n\tcursor: -webkit-zoom-in;\n\tcursor: -moz-zoom-in;\n\tcursor: zoom-in;\n  }\n\n  .mfp-auto-cursor {\n\t.mfp-content {\n\t  cursor: auto;\n\t}\n  }\n\n  .mfp-close,\n  .mfp-arrow,\n  .mfp-preloader,\n  .mfp-counter {\n\t-webkit-user-select: none;\n\t-moz-user-select: none;\n\tuser-select: none;\n  }\n\n  // Hide the image during the loading\n  .mfp-loading {\n\t&.mfp-figure {\n\t  display: none;\n\t}\n  }\n\n}"],"sourceRoot":"/source/"}